:root {
  --color-primary: #ec4755;
  --color-secondary: #a12c34;
  --color-tertiary: #99a0a3;
  --color-quaternary: #FFD700;
}

a {
  --tw-text-opacity: 1;
  color: rgb(185 28 28 / var(--tw-text-opacity, 1));
}

a:hover {
  border-bottom-width: 2px;
  --tw-border-opacity: 1;
  border-color: rgb(185 28 28 / var(--tw-border-opacity, 1));
}


.content p {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}

.content ul {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}

.ticker-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f0f0f0;
  padding: 2px;
  border: 1px solid #ccc;
}

.ticker-text {
  font-size: 16px;
  color: #333;
}

/* Custom scrollbar styles */
.scrollbar-hide {
  -ms-overflow-style: none;  /* Internet Explorer 10+ */
  scrollbar-width: none;  /* Firefox */
}
.scrollbar-hide::-webkit-scrollbar {
  display: none;  /* Safari and Chrome */
}

/* Simple custom scrollbar for municipality list */
.custom-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: #cbd5e1 #f8fafc;
}

.custom-scrollbar::-webkit-scrollbar {
  width: 6px;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background: #f8fafc;
  border-radius: 6px;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background: #cbd5e1;
  border-radius: 6px;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #94a3b8;
}

/* Line clamp utility for text truncation */
.line-clamp-2 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}

@keyframes scroll {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}

.animate-scroll {
  animation: scroll 40s linear infinite;
}

.fit-content-span {
  width: -moz-fit-content;
  width: fit-content;
}

.backdrop-blur-custom {
  backdrop-filter: blur(4px);
}


/* Professional Install App Button Animations */
@keyframes shimmer {
  0% { transform: translateY(-100%); }
  100% { transform: translateY(100%); }
}

.animate-shimmer {
  animation: shimmer 0.6s ease-out;
}

/* Hero refinements for more professional spacing & contrast */
.hero-tight {
  max-width: 1100px;
  margin: 0 auto;
  padding-top: 3.5rem;
  padding-bottom: 3.5rem;
}
.hero-tight h1 {
  letter-spacing: -0.02em;
  font-weight: 900;
  color: #0f172a;
}


.gyg-widget-container {
  min-height: 420px;
  max-width: 800px;
  margin: 3rem auto 2rem auto;
  background: var(--color-primary, #fff);
  border-radius: 1.25rem;
  box-shadow: 0 6px 32px 0 rgba(44, 62, 80, 0.10), 0 1.5px 4px 0 rgba(44, 62, 80, 0.04);
  padding: 2.5rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  border: 1px solid var(--color-tertiary, #e5e7eb);
  transition: box-shadow 0.2s;
}

.gyg-widget-container:hover,
.gyg-widget-container:focus-within {
  box-shadow: 0 12px 48px 0 rgba(44, 62, 80, 0.16), 0 3px 8px 0 rgba(44, 62, 80, 0.08);
  border-color: var(--color-secondary, #a12c34);
}

.blog-content h1 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
}
.blog-content h2 {
  font-size: 1.5rem;
  font-weight: 600;
  margin-top: 2rem;
}
.blog-content p {
  margin-bottom: 1rem;
  line-height: 1.8;
}
.blog-content ul {
  list-style: disc;
  padding-left: 1.5rem;
  margin-bottom: 1rem;
}
.blog-content blockquote {
  background: #f9f9f9;
  padding: 1rem;
  border-left: 4px solid #ec4755;
  font-style: italic;
  color: #555;
  margin: 1.5rem 0;
}
.blog-content img {
  display: block;
  margin: 2rem auto;
  max-width: 100%;
  border-radius: 1.25rem;
  box-shadow: 0 4px 24px rgba(0,0,0,0.10), 0 1.5px 6px rgba(8,120,254,0.10);
  border: 2px solid #e0e7ef;
  background: #fff;
  transition: box-shadow 0.3s, border-color 0.3s;
}
